.footer {
  padding: 100px 0;
  overflow: hidden;
}

@media (max-width: 768px) {
  .footer {
    flex-direction: column-reverse;
  }
}

.footer__logo {
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .footer__logo {
    margin-bottom: 0;
  }
}

.footer__logo a {
  color: #141718;
  transition: color .3s ease-in-out;
}

.footer__logo a:hover {
  color: #d41a3b;
}

.footer .social {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.footer .social_link {
  width: 48px;
}

.footer .social_link svg {
  transition: fill .3s ease-in-out;
  width: 100%;
  height: 100%;
}

.footer .social_link:hover svg {
  fill: #d41a3b;
}

.footer__contact {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  line-height: 1.44;
  font-weight: 400;
}

.footer__contact h3 {
  font-weight: 600;
  color: #52575C;
  font-size: 18px;
  margin-bottom: 10px;
}

.footer__contact a {
  font-size: 18px;
  color: #141718;
  font-weight: 400;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.footer__contact a:hover {
  color: #d41a3b;
  transform: translateX(7px);
}

.footer_top {
  width: 30%;
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: flex-start;
  justify-content: flex-start;
}

@media (max-width: 768px) {
  .footer_top {
    width: 100%;
    margin-top: 20px;
    flex-direction: row;
    align-items: flex-end;
  }
}

.footer_bot {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 30px;
}

@media (max-width: 768px) {
  .footer_bot {
    flex-direction: column;
  }
}
